<template>
  <div class="home">
    <music></music>
    <topBar></topBar>
    <!-- 内容区 -->

    <main class="main">
      <el-row type="flex" justify="center">
        <el-col :span="16" class="main_content">
          <!-- <el-card> -->
               <slot></slot>
            <!-- 内容slot 展示区 -->
          <!-- </el-card> -->
        </el-col>
      </el-row>
    </main>

    <!-- 尾部 -->
    <footbar></footbar>
  </div>
</template>

<script>
// import DPlayer from "dplayer";
import music from "../music/music.vue";

import topBar from "../topBar/index.vue";
import footbar from "../footbar/footbar.vue";

export default {
  name: "home",
  components: { topBar, footbar,music},
  data() {
    return {
      activeIndex: "1",
      changeColor: false,
      canva: false,
      imgData: [
        { title: "1", url: "https://blinkfox.github.io/medias/banner/1.jpg" },
        { title: "2", url: "https://t1.picb.cc/uploads/2021/04/04/ZBnzty.jpg" },
        { title: "3", url: "https://t1.picb.cc/uploads/2021/04/04/ZBnLWX.jpg" },
      ],
    };
  },
  created() {},
  methods: {
    // 背景canvas
  },
};
</script>

<style  lang="less" scoped>
.home {
  position: relative;
  z-index: 1;
  .right {
    float: right;
  }
  .left {
    float: left;
  }
  .bgColor {
    background-image: linear-gradient(to right, #4cbf30 0%, #0f9d58 100%);
  }

  // 内容区
  .main {
    min-height: 772px;
    margin-top: -60px;
    /deep/.el-card__body {
      min-height: 200px;
    }
    // .title {
    //   margin-bottom: 20px;
    //   font-size: 2rem;
    //   font-weight: 700;
    // }
    // .main_content {
    //   .dream {
    //     margin-top: 20px;
    //     margin-bottom: 40px;
    //     background: white;
    //   }
    // }
  }
}
</style>



