<template>
  <div class="navbox">
    <!-- <music></music> -->
    <!-- 导航栏 -->
    <el-row class="menu">
      <div :class="{ bgColor: changeColor, menu_cont: true }">
        <div class="menu_cont_item">
          <div class="menu_cont_item_left .left brand-logo">
            <img :src="$staticUrl + 'static/img/huli.png'" />
            狐行千里
          </div>
          <div class="menu_cont_item_right">
            <el-menu
              router
              :default-active="$route.path"
              mode="horizontal"
              @select="handleSelect"
            >
              <el-menu-item index="firstpage">
                <i class="el-icon-s-home"></i>首页</el-menu-item
              >

              <el-menu-item index="2">
                <i class="el-icon-menu"></i>我的工作台</el-menu-item
              >
              <el-menu-item index="me">
                <i class="el-icon-user-solid"></i>关于我</el-menu-item
              >
            </el-menu>
          </div>
        </div>
      </div>
    </el-row>

    <!-- banner图 -->
    <el-row>
      <div class="banner bgcolor bgCover"></div>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "navbox",
  data() {
    return {
      activeIndex: "1",
      changeColor: false,
      canva: false,
      imgData: [
        { title: "1", url: "https://blinkfox.github.io/medias/banner/1.jpg" },
        { title: "2", url: "https://t1.picb.cc/uploads/2021/04/04/ZBnzty.jpg" },
        { title: "3", url: "https://t1.picb.cc/uploads/2021/04/04/ZBnLWX.jpg" },
      ],
    };
  },
  created() {
    this.listen()
  },
  methods: {
    listen() {
      var _this = this;
      window.addEventListener("scroll", function () {
        var scrollTop =
          document.documentElement.scrollTop || document.body.scrollTop;
        if (scrollTop > 100) {
          //滚动条的高度，可以动态获取也可以写死
          //这里写要执行的操作
          _this.changeColor = true;
        } else {
          _this.changeColor = false;
        }
      });
    },
    // 初始化视频
    video() {
      const dp = new DPlayer({
        container: document.getElementById("dplayer"),
        autoplay: false,
        hotkey: true,
        preload: "auto",
        video: {
          url: "https://blinkfox.github.io/medias/videos/demo.mp4",
          pic: "https://t1.picb.cc/uploads/2021/04/04/ZBlUGj.jpg",
        },
      });
      // dp.seek(1);
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
  },
};
</script>

<style  lang="less" scoped>
.navbox {
  .menu {
    height: 64px;
    position: relative;
    z-index: 9;
    color: white;
    font-weight: 900;
    .menu_cont {
      width: 100%;
      -webkit-box-shadow: none;
      box-shadow: none;
      position: fixed;
      .menu_cont_item {
        position: relative;
        height: 100%;
        max-width: 1125px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;

        .menu_cont_item_right {
          .el-menu {
            background-color: transparent !important;
            background-image: none;
            &.el-menu.el-menu--horizontal {
              border-bottom: none;
            }
            li {
              color: white;
              background: transparent !important;

              i {
                color: white !important;
              }
              &.is-active {
                background: transparent !important;
                border-bottom: 0px transparent;
              }
              &.is-active:hover {
                background: rgba(0, 0, 0, 0.1) !important;
              }
              &:not(is-active):hover {
                background: rgba(0, 0, 0, 0.1) !important;
              }
            }
          }
        }
        .menu_cont_item_left {
          line-height: 64px;
          font-size: 22px;
          font-weight: 400;
          /deep/i {
            margin-right: 5px;
            width: 24px;
            text-align: center;
            font-size: 18px;
            vertical-align: middle;
            color: white !important;
          }
          img {
            height: 45px;
            vertical-align: middle;
            padding-bottom: 6px;
          }
        }
      }
    }
  }

  .banner {
    height: 100%;
    width: 100%;
    margin-top: -64px;
    /deep/.img {
      background-position: center center;
      background-size: cover;
      background-repeat: no-repeat;
      transform: translate3d(0px, 0px, 0px);
    }
  }

  .right {
    float: right;
  }
  .left {
    float: left;
  }
  .bgColor {
    background-image: linear-gradient(to right, #4cbf30 0%, #0f9d58 100%);
  }
  .bgCover {
    background-position: center center;
    background-size: cover;
    transform: translate3d(0px, 0px, 0px);
    background-image: url("https://blinkfox.github.io/medias/banner/1.jpg");
    height: 60vh;
  }
  // .colorCover{

  // }

  // 内容区
  .main {
    min-height: 772px;
    margin: 10px;
    .title {
      margin-bottom: 20px;
      font-size: 2rem;
      font-weight: 700;
    }
    .main_content {
      .dream {
        margin-top: 20px;
        margin-bottom: 40px;
        background: white;
      }
    }
  }
}
</style>



