<template>
  <div class="music">
    <div id="player"></div>
  </div>
</template>

<script>
import "aplayer/dist/APlayer.min.css";
import APlayer from "aplayer";

export default {
  name: "music",
  data() {
    return {
      list: [],
    };
  },
  mounted() {
    this.getMucdata(); // 获取歌词数据
  },
  methods: {
    music() {
      let option = {
        container: document.getElementById("player"),
        mini: true,
        fixed: true,
        autoplay: false,
        theme: "#FADFA3",
        loop: "all",
        order: "list",
        preload: "auto",
        volume: 0.1,
        mutex: true,
        listFolded: false,
        listMaxHeight: 90,
        lrcType: 3,
        audio: [],
      };
      option.audio = this.list;
      const ap = new APlayer(option);
      ap.list.hide();
    },

    getMucdata() {
      this.$axios
        .get(
          "https://api.i-meto.com/meting/api?server=netease&type=playlist&id=503838841&r=0.001315273867846134"
        )
        .then((res) => {
          // name: 'name2',
          // artist: 'artist2',
          // url: 'url2.mp3',
          // cover: 'cover2.jpg',
          // lrc: 'lrc2.lrc',
          // theme: '#46718b'
          if (res.data) {
            res.data.forEach((item) => {
              item.name = item.author;
              item.artist = item.author;
              item.url = item.url;
              item.cover = item.pic;
              item.lrc = item.lrc;
              item.theme = "#46718b";
            });
          }
          this.list = res.data;
          this.music(); //音乐初始化
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

<style lang="less" scoped>
/deep/#player .aplayer-list ol {
  height: 200px;
  overflow-y: scroll;
}

/deep/.aplayer.aplayer-narrow .aplayer-body {
  left: -66px;
  &:hover {
    left: 0px;
  }
}


</style>


